.table{
    max-width: 740px;
    width: 100%;
    text-align:  center;

    td,th{
        border-spacing: 0;
        border-top-width:0;
        border-bottom-width: 0;
        padding: 1rem;
    }
    th{
        color: #fff;
        background-color: #433633;
        cursor: pointer;
    }
    tr{
        background-color: #DECBB7;
    }
    tr:nth-child(2n+1){
        color: #fff;
        background-color: #8F857D;
    }
}